(function() {
  'use strict';

  angular
    .module('routeA11y', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria',
      'ngResource', 'ui.router', 'ui.bootstrap']);

})();

(function () {
  'use strict';

  angular
    .module('routeA11y')
    .directive('autoFocus', autoFocus);

  /** @ngInject */
  function autoFocus($rootScope) {
    var directive = {
      restrict: 'A',
      link: linkFunc
    };

    return directive;

    function linkFunc(scope, element) {
      $rootScope.$on('$viewContentLoaded',
        function (event) {
          element[0].focus();
        });
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('routeA11y')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController() {
      var vm = this;
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('routeA11y')
    .directive('calculator', calculator);

  /** @ngInject */
  function calculator() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/calculator/calculator.html',
      controller: CalculatorController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function CalculatorController($timeout) {
      var vm = this;
      vm.calculate = calculate;

      function calculate() {
        vm.calculated = false;
        $timeout(function() {
          vm.calculated = true;
          vm.result = Math.floor((Math.random() * 10) + 1) + " + "  + Math.floor((Math.random() * 10) + 1);
        }, 1000);
      }
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('routeA11y')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function () {
  'use strict';

  angular
    .module('routeA11y')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {

    $stateProvider
      .state('base', {
        abstract: true,
        views: {
          "nav": {
            templateUrl: 'app/components/header/header.html'
          },
          "main": {
            template: '<ui-view></ui-view>'
          },
          "footer": {
            templateUrl: 'app/components/footer/footer.html'
          }
        }
      })
      .state('base.index', {
        url: "/",
        templateUrl: 'app/states/home/home.html'
      })
      .state('base.route1', {
        url: "/route1",
        templateUrl: 'app/states/route1/route1.html'
      })
      .state('base.route2', {
        url: "/route2",
        templateUrl: 'app/states/route2/route2.html'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('routeA11y');

})();

(function() {
  'use strict';

  angular
    .module('routeA11y')
    .config(config);

  /** @ngInject */
  function config($logProvider) {
    // Enable log
    $logProvider.debugEnabled(true);

  }

})();

