$templateCache.put("app/states/home/home.html","<div id=\"home\"><div class=\"well\"><h1>Overview</h1></div><p>This is my attempt to add accessibility to angular SPA application. Router UI is used to navigate between different application states. Therefore, the navigation and footer stays the same and only the main area gets updated.</p>When a user clicks on a navigation link that changes the state the goal is:<ul><li>To push the dynamic content to screen reader to be announced</li><li>Move focus should to the new dynamic content</li></ul><h3>Push dynamic content</h3>I tried to use <b>aria-live</b> but somehow it did not work for this case. Role <b>alert</b> made the magic: <img class=\"img-responsive rolealert\" src=\"assets/images/rolealert.png\" alt=\"code with role alert\"><h3>Focus management</h3><b>AutoFocus</b> directive was added to dynamically move the focus when on the success routing event.<h3>How to test</h3>I tested the solution using Voice Over on Mac, NVDA on Windows and ChromVox plugin. Here is screen log from Voice Over when I clicked on \'Route2\' navigation link. As you see the screen reader was able to announce the new dynamic content automatically. <img class=\"img-responsive\" src=\"assets/images/voiceover.png\" alt=\"voice over screen capture\"><h3>Suggestions</h3>Please let me know if you solved this problem using any other means.</div>");
$templateCache.put("app/states/route1/route1.html","<div id=\"result1\"><div class=\"well\"><h1 class=\"text-center\">Route1 - Live Aria</h1></div><p>Route1 demonstrates live aria usage. Upon the button click a new region is opened and screen reader announced it. See the screen log of Voice Over when calculate button is clicked: <img class=\"img-responsive rolealert\" src=\"assets/images/livearia.png\" alt=\"screen log with live aria\"></p><calculator></calculator></div>");
